<template>
  <div id="page-top">
    <!-- Navigation-->
    <nav class="navbar navbar-expand-lg navbar-dark fixed-top" id="mainNav">
      <div class="container">
        <a class="navbar-brand" href="#page-top"
          ><img src="../../assets/img/navbar-logo.svg" alt="..."
        /></a>
        <button
          class="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarResponsive"
          aria-controls="navbarResponsive"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          Menu
          <i class="fas fa-bars ms-1"></i>
        </button>
        <div class="collapse navbar-collapse" id="navbarResponsive">
          <ul class="navbar-nav text-uppercase ms-auto py-4 py-lg-0">
            <!-- <li class="nav-item">
              <a class="nav-link" href="#services">Services</a>
            </li> -->
            <li class="nav-item"><a class="nav-link" href="#GroomBride">신랑♡신부</a></li>
            <li class="nav-item">
              <a class="nav-link" href="#HelloPage">인사말</a>
            </li>

            <li class="nav-item">
              <a class="nav-link" href="#portfolio">Portfolio</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#contact">Contact</a>
            </li>
          </ul>
        </div>
      </div>
    </nav>
    <!-- Masthead-->
    <header class="masthead" style="height: 1024px; position: relative;">
      <canvas id="canvas" style="position: absolute; top:0px; left:0px;"></canvas>
        <div class="container">
          <div class="masthead-subheading">D-DAY : {{ dDay }}</div>
          <div class="masthead-comment">You're Invited</div>
          <div class="masthead-heading ">현준♥혜인 결혼합니다.</div>
          <div class="masthead-subheading">2024.03.30 오후 3시</div>
          <div class="masthead-subheading">합정 웨딩 시그니처</div>
          <!-- <a class="btn btn-primary btn-sm text-uppercase" href="#services">Tell Me More</a> -->
        </div>

    </header>
  </div>
</template>
<script>
export default {
  name: "HeaderNavVar",
  data() {
    return {
      dDay: "",
    };
  },
  methods: {
    diffDay() {
      const dDayTime = new Date("2024-03-30T15:00:00+0900");
      const todayTime = new Date();

      const diff = dDayTime - todayTime;

      const diffDay = Math.floor(diff / (1000 * 60 * 60 * 24));
      const diffHour = Math.floor((diff / (1000 * 60 * 60)) % 24);
      const diffMin = Math.floor((diff / (1000 * 60)) % 60);
      const diffSec = Math.floor((diff / 1000) % 60);
      this.dDay = diffDay + "일 " + diffHour + "시간 " + diffMin + "분 " + diffSec + "초 ";
    },
  },
  mounted() {
    this.diffDay();
    setInterval(this.diffDay, 1000);

    const canvas = document.querySelector('#canvas');
    canvas.width = window.innerWidth
    canvas.height = window.innerHeight
    const ctx = canvas.getContext('2d')

    const TOTAL = 150
    const petalArray = []

    const petalImg = new Image()
    petalImg.src = require('../../assets/img/petal.png'),
    petalImg.onload = () => {
      for (let i = 0; i < TOTAL; i++) {
        petalArray.push(new Petal())
      }
      render()
    }

    function render() {
      ctx.clearRect(0, 0, canvas.width, canvas.height)
      petalArray.forEach(petal => {
        petal.animate()
      })

      window.requestAnimationFrame(render)
    }

    window.addEventListener('resize', () => {
      canvas.width = window.innerWidth
      canvas.height = window.innerHeight
    })

    // 벚꽃 잎 클래스
    class Petal {
      constructor() {
        this.x = Math.random() * canvas.width
        this.y = Math.random() * canvas.height * 2 - canvas.height
        this.w = 30 + Math.random() * 15
        this.h = 20 + Math.random() * 10
        this.opacity = this.w / 100
        this.xSpeed = 2 + Math.random()
        this.ySpeed = 2 + Math.random() * 0.5
        this.flip = Math.random()
        this.flipSpeed = Math.random() * 0.03
      }

      draw() {
        if (this.y > canvas.height || this.x > canvas.width) {
          this.x = -petalImg.width
          this.y = Math.random() * canvas.height * 2 - canvas.height
          this.xSpeed = 2 + Math.random()
          this.ySpeed = 1 + Math.random() * 0.5
          this.flip = Math.random()
        }
        ctx.globalAlpha = this.opacity
        ctx.drawImage(
          petalImg,
          this.x,
          this.y,
          this.w * (0.66 + (Math.abs(Math.cos(this.flip)) / 3)),
          this.h * (0.8 + (Math.abs(Math.sin(this.flip)) / 2)),
        )
      }

      animate() {
        this.x += this.xSpeed
        this.y += this.ySpeed
        this.draw()
        this.flip += this.flipSpeed
      }
    }
  },
};

</script>
