<template>
  <div id="app" style="background-color: rgb(233, 231, 231)">
    <HeaderNavVar />
    <GroomBride />
    <PhotoGallery />
    <HelloPage />
  </div>
</template>

<script>
import HeaderNavVar from "./components/layout/Header.vue";
import HelloPage from "./components/layout/HelloPage.vue";
import GroomBride from "./components/layout/GroomBride.vue";
import PhotoGallery from "./components/layout/PhotoGallery.vue";

export default {
  name: "App",
  data() {
    return {
      isMobile: false,
      pcStandard: 769, // PC 화면의 Viewport 기준점
      mobileOption: {
        // 모바일 옵션 지정
        slidesPerView: 1,
        effect: "fade",
        fadeEffect: {
          crossFade: true,
        },
        loop: true,
      },
      pcOption: {
        // PC 옵션 지정
        slidesPerView: 3,
      },
    };
  },
  methods: {
    checkIsMobile: function () {
      this.isMobile = (window.innerWidth < this.pcStandard)
    },
    handleResize: function () {
      this.$nextTick(function () {
        this.checkIsMobile()
      })
    }
  },
  components: {
    HeaderNavVar,
    GroomBride,
    HelloPage,
    PhotoGallery,
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>
