<template>
  <div class="thumb-example" style="background-color: rgb(233, 231, 231); margin-bottom: 30px;">
    <h1>사진첩</h1>
    <!-- swiper1 -->
    <swiper class="swiper gallery-top" :options="swiperOptionTop" ref="swiperTop">
      <swiper-slide class="slide-1"></swiper-slide>
      <swiper-slide class="slide-2"></swiper-slide>
      <swiper-slide class="slide-3"></swiper-slide>
      <swiper-slide class="slide-4"></swiper-slide>
      <swiper-slide class="slide-5"></swiper-slide>
      <swiper-slide class="slide-6"></swiper-slide>
      <swiper-slide class="slide-7"></swiper-slide>
      <swiper-slide class="slide-8"></swiper-slide>
      <swiper-slide class="slide-9"></swiper-slide>
      <swiper-slide class="slide-10"></swiper-slide>
      <swiper-slide class="slide-11"></swiper-slide>
      <div class="swiper-button-next swiper-button-white" slot="button-next"></div>
      <div class="swiper-button-prev swiper-button-white" slot="button-prev"></div>
    </swiper>
    <!-- swiper2 Thumbs -->
    <swiper class="swiper gallery-thumbs" :options="swiperOptionThumbs" ref="swiperThumbs">
      <swiper-slide class="slide-1"></swiper-slide>
      <swiper-slide class="slide-2"></swiper-slide>
      <swiper-slide class="slide-3"></swiper-slide>
      <swiper-slide class="slide-4"></swiper-slide>
      <swiper-slide class="slide-5"></swiper-slide>
      <swiper-slide class="slide-6"></swiper-slide>
      <swiper-slide class="slide-7"></swiper-slide>
      <swiper-slide class="slide-8"></swiper-slide>
      <swiper-slide class="slide-9"></swiper-slide>
      <swiper-slide class="slide-10"></swiper-slide>
      <swiper-slide class="slide-11"></swiper-slide>
    </swiper>
  </div>
</template>

<script>
  import { Swiper, SwiperSlide } from 'vue-awesome-swiper'
  import 'swiper/css/swiper.css'

  export default {
    name: 'swiper-example-thumbs-gallery',
    title: 'Thumbs gallery with Two-way control',
    components: {
      Swiper,
      SwiperSlide
    },
    data() {
      return {
        swiperOptionTop: {
          loop: true,
          loopedSlides: 5, // looped slides should be the same
          spaceBetween: 10,
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev'
          }
        },
        swiperOptionThumbs: {
          loop: true,
          loopedSlides: 5, // looped slides should be the same
          spaceBetween: 10,
          centeredSlides: true,
          slidesPerView: 'auto',
          touchRatio: 0.2,
          slideToClickedSlide: true
        }
      }
    },
    mounted() {
      this.$nextTick(() => {
        const swiperTop = this.$refs.swiperTop.$swiper
        const swiperThumbs = this.$refs.swiperThumbs.$swiper
        swiperTop.controller.control = swiperThumbs
        swiperThumbs.controller.control = swiperTop
      })
    }
  }
</script>

<style lang="scss" scoped>
  .thumb-example {
    height: 480px;
    background-color: white;
  }

  .swiper {
    .swiper-slide {
      background-size: cover;
      background-position: center;

      &.slide-1 {
        background-image:url('../../assets/img/gallery/1.jpg');
      }
      &.slide-2 {
        background-image:url('../../assets/img/gallery/2.jpg');
      }
      &.slide-3 {
        background-image:url('../../assets/img/gallery/3.jpg');
      }
      &.slide-4 {
        background-image:url('../../assets/img/gallery/4.jpg');
      }
      &.slide-5 {
        background-image:url('../../assets/img/gallery/5.jpg');
      }
      &.slide-6 {
        background-image:url('../../assets/img/gallery/6.jpg');
      }
      &.slide-7 {
        background-image:url('../../assets/img/gallery/7.jpg');
      }
      &.slide-8 {
        background-image:url('../../assets/img/gallery/8.jpg');
      }
      &.slide-9 {
        background-image:url('../../assets/img/gallery/9.jpg');
      }
      &.slide-10 {
        background-image:url('../../assets/img/gallery/10.jpg');
      }
      &.slide-11 {
        background-image:url('../../assets/img/gallery/11.jpg');
      }
    }

    &.gallery-top {
      height: 80%;
      width: 100%;
    }
    &.gallery-thumbs {
      height: 33%;
      box-sizing: border-box;
      padding: 10px;
    }
    &.gallery-thumbs .swiper-slide {
      width: 33%;
      height: 100%;
      opacity: 1;
    }
    &.gallery-thumbs .swiper-slide-active {
      opacity: 1;
    }
  }
</style>